/**
 * Right region only
 */
@include get-location($type: vertical, $region: right) {

  .escort-aside-content {

    .escort-grid {

      li {

        a {
          @include button-style($region: right, $transparent: true) {
            border: 1px solid darken($bg-color-right, 5%);
          };
        }
      }
    }
  }
}
